@charset "UTF-8";
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif; }

/**
 * 清除ul ol的样式 
 */
ul,
ol,
ul li,
ol li {
  list-style: none !important; }

/* 初始化 */
body,
div,
ul,
li,
ol,
h1,
h2,
h3,
h4,
h5,
h6,
input,
textarea,
select,
p,
dl,
dt,
dd,
a,
img,
button,
form,
table,
th,
tr,
td,
tbody,
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  margin: 0;
  padding: 0;
  border: none; }

/**
  * 设置行高 
  */
div,
p,
a {
  color: unset;
  text-decoration: none !important; }

/* body 样式*/
html,
body {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0px;
  left: 0px;
  font-family: "SourceHanSansCN-Light","Source Han Sans CN", "微软雅黑", "Microsoft YaHei" !important;
  margin: 0 auto;
  -webkit-tap-highlight-color: transparent;
  background: #f7f7f7; }

/*css-modules通用样式*/
:global(.childcenter) {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center; }

:global(.childcolumn) {
  -ms-flex-direction: column;
      flex-direction: column; }

:global(.childrow) {
  -ms-flex-direction: column;
      flex-direction: column; }

:global(.childcontentstart) {
  -ms-flex-pack: start;
      justify-content: flex-start; }

:global(.childcontentend) {
  -ms-flex-pack: end;
      justify-content: flex-end; }

:global(.childalignstart) {
  -ms-flex-align: start;
      align-items: flex-start; }

:global(.childalignend) {
  -ms-flex-align: end;
      align-items: flex-end; }



